import React from 'react';
import { Link, withRouter } from 'react-router-dom';
import { routes } from '../AnimatedSwitch/AnimatedSwitch';
import './Loader.css';

const Loader = (props) => {
    const { location } = props;
    let currentScreen = location.pathname;

    let prev = routes[routes.indexOf(currentScreen) - 1];
    let next = routes[routes.indexOf(currentScreen) + 1];

    return (
        <div className='page-control'>
            {prev ?
                <Link to={{ pathname: prev, state: { previousScreen: routes.indexOf(currentScreen) } }} className='prev' >
                    <i className='bx bx-left-arrow-alt' ></i>
                    <span className='prev-txt'>{(prev === '/' ? '/home' : prev).slice(1)}</span>
                </Link> :
                null
            }

            {next ?
                <Link to={{ pathname: next, state: { previousScreen: routes.indexOf(currentScreen) } }} className='next'>
                    <i className='bx bx-right-arrow-alt'></i>
                    <span className='next-txt'>{next.slice(1)}</span>
                </Link> :
                null
            }
        </div>
    );
}

export default withRouter(Loader);
