import React, { useState } from 'react';
import { Link, NavLink, withRouter } from 'react-router-dom';
import { routes } from '../AnimatedSwitch/AnimatedSwitch';

const Header = (props) => {
    const [showMenu, setShowMenu] = useState(false);
    const { location } = props;
    let currentScreen = location.pathname;

    const linkAction = (event, i) => {
        /*Remove menu mobile*/
        const navMenu = document.getElementById('nav-menu');
        navMenu.classList.remove('show');
    }

    return (
        <header className="l-header">
            <nav className="nav bd-grid">
                <Link to={{ pathname: "/", state: { previousScreen: routes.indexOf(currentScreen) }, nav: true }} className="nav__logo">Shubham</Link>

                <div className={"nav__menu" + (showMenu ? ' show' : '')} id="nav-menu">
                    <ul className="nav__list" onClick={linkAction}>
                        <li className="nav__item"><NavLink exact to={{ pathname: "/", state: { previousScreen: routes.indexOf(currentScreen) }, nav: true }} className="nav__link" >Home</NavLink></li>
                        <li className="nav__item"><NavLink to={{ pathname: "/about", state: { previousScreen: routes.indexOf(currentScreen) }, nav: true }} className="nav__link">About</NavLink></li>
                        <li className="nav__item"><NavLink to={{ pathname: "/skills", state: { previousScreen: routes.indexOf(currentScreen) }, nav: true }} className="nav__link">Skills</NavLink></li>
                        <li className="nav__item"><NavLink to={{ pathname: "/work", state: { previousScreen: routes.indexOf(currentScreen) }, nav: true }} className="nav__link">Work</NavLink></li>
                        <li className="nav__item"><NavLink to={{ pathname: "/contact", state: { previousScreen: routes.indexOf(currentScreen) }, nav: true }} className="nav__link" >Contact</NavLink></li>
                    </ul>
                </div>

                <div className="nav__toggle" id="nav-toggle" onClick={() => setShowMenu(!showMenu)}>
                    <i className='bx bx-menu'></i>
                </div>
            </nav>
        </header>
    );
}

export default withRouter(Header);