import React from 'react';
import { Route, Switch, withRouter } from "react-router-dom";
import { TransitionGroup, CSSTransition } from "react-transition-group";
import Home from '../Home/Home';
import About from '../About/About';
import Skills from '../Skills/Skills';
import Work from '../Work/Work';
import Contact from '../Contact/Contact';
import './AnimatedSwitch.css';

const AnimatedSwitch = ({ location }) => {
    let currentScreen = routes.indexOf(location.pathname);
    const { state, nav } = location;
    const previousScreen = state ? state.previousScreen : 0;

    const animationClassNames = nav ?
        currentScreen > previousScreen ? 'slide-up' : currentScreen < previousScreen ? 'slide-down' : '' :
        currentScreen > previousScreen ? 'slide-forward' : 'slide-backward';

    return (
        <TransitionGroup
            childFactory={child =>
                React.cloneElement(child, {
                    classNames: animationClassNames
                })
            }>
            <CSSTransition key={location.key} classNames={animationClassNames} timeout={1000}>
                <div className='l-main'>
                    <Switch location={location}>
                        <Route exact path='/' component={Home} />
                        <Route exact path='/about' component={About} />
                        <Route exact path='/skills' component={Skills} />
                        <Route exact path='/work' component={Work} />
                        <Route exact path='/contact' component={Contact} />
                    </Switch>
                </div>
            </CSSTransition>
        </TransitionGroup>
    )
};

export default withRouter(AnimatedSwitch);

export const routes = ['/', '/about', '/skills', '/work', '/contact',];